<template>
   <div class='root'>
      <action-bar title='创建优惠券'></action-bar>
      <div class="top">
         <div class="tip">优惠券一旦设置则不允许修改,请谨慎设计券的面额;如果遇到紧急情况,请联系易批花运营负责人</div>
      </div>
      <div class='container'>
         <scroll-view class='content'>
            <div class="content-box">
               <div>券的类型</div>
               <div class="coupon-group">
                  <div class="coupon-checked">店铺满减券</div>
               </div>
               <div class="content-tip">买家可以在店铺首页领取优惠券</div>
               <div class="detail">
                  <span class="detail-title">券的名称</span>
                  <input maxlength="10" type="text" v-model="title" class="detail-content"
                     placeholder="请输入优惠券的名称10字以内" />
               </div>
               <div class="detail">
                  <span class="detail-title">券的面值</span>
                  <input maxlength="4" type="number" v-model="amount" class="detail-content" placeholder="1-5000" />
               </div>
               <div class="detail">
                  <span class="detail-title">使用条件</span>
                  <input maxlength="4" type="number" class="detail-content" placeholder="用券最低订单金额"
                     v-model="minAmount" />
               </div>
               <div class="detail">
                  <span class="detail-title">发行张数</span>
                  <input maxlength="4" type="number" class="detail-content" placeholder="请填写1-9999张您要发行的优惠券张数"
                     v-model="issuedNumber" />
               </div>
               <div class="detail">
                  <span class="detail-title">每人限领</span>
                  <div class="detail-content">
                     <div class="limit-box">
                        <img class="img-limit" src="@/assets/icon_coupon_dec.png" @click="limitReduce" />
                        <input type="number" v-model="limitPerson" class="limit-value" disabled />
                        <img class="img-limit" src="@/assets/icon_coupon_add.png" @click="limitAdd" />
                     </div>
                  </div>
               </div>
               <div class="detail">
                  <span class="detail-title">开始时间</span>
                  <div class="detail-content" @click="choiceDate('start')">
                     <div class="choice-date">
                        <input v-model="beginTime" type="text" disabled placeholder="请选择" />
                        <img class="img-right" src="@/assets/arrow_right_icon.png" />
                     </div>
                  </div>
               </div>
               <div class="detail" @click="choiceDate('end')">
                  <span class="detail-title">结束时间</span>
                  <div class="detail-content">
                     <div class="choice-date">
                        <input v-model="endTime" type="text" disabled placeholder="请选择" />
                        <img class="img-right" src="@/assets/arrow_right_icon.png" />
                     </div>
                  </div>
               </div>
            </div>
         </scroll-view>
      </div>
      <div class="bottom">
         <div class="agree">
            <van-checkbox class="ck" icon-size="16px" v-model="isPrivacy" checked-color="#42D18A"></van-checkbox>
            <div>同意<a :href="$store.state.baseUrl + '/v1/easyflower/h5_1/agree/agree.html'" target="_blank"
                  class="privacy">《易批花优惠券发布协议》</a></div>
         </div>
         <div @click="clickSubmit" :class="['release', isPrivacy ? 'release-true' : 'release-false']">发布</div>
      </div>
      <date-picker-popup type="date" ref="startPopup" @confirm="confirmStartDate"></date-picker-popup>
      <date-picker-popup type="date" ref="endPopup" @confirm="confirmEndDate"></date-picker-popup>
      <van-popup v-model="show" class="sure-popup">
         <div class="sure-popup-title">确定发布优惠券</div>
         <div class="sure-popup-content">请核对优惠券金额</div>
         <div class="sure-popup-tip">注：优惠券一旦设置不可更改</div>
         <div class="divider"></div>
         <div class="sure-popup-operation">
            <div class="operation-cannel" @click="popupCannel">再想想</div>
            <div class="divider-h"></div>
            <div class="operation-ok"  @click="popupOk">发布</div>
         </div>
      </van-popup>
   </div>
</template>
<script>

import { Checkbox, Popup } from 'vant'
import DatePickerPopup from '@/components/util/DatePickerPopup.vue'
import { getTimeMaximum } from '@/utils/dateTool'
import { addBusinessCoupon } from '@/http/api'

export default {
  name: '',
  components: {
    [Checkbox.name]: Checkbox,
    DatePickerPopup,
    [Popup.name]: Popup
  },
  data () {
    return {
      startTimeLimit: '', // 开始时间限制
      isPrivacy: false, // 是否同意协议
      title: '', // 券的名称
      amount: '', // 券的面值
      minAmount: '', // 券的使用条件
      issuedNumber: '', // 发行张数
      limitPerson: 1, // 每人限领
      beginTime: '', // 开始时间
      endTime: '', // 结束事件
      show: false
    }
  },
  methods: {
    popupCannel () {
      this.show = false
    },
    popupOk () {
      const data = {
        title: this.title,
        amount: this.amount,
        minAmount: this.minAmount,
        issuedNumber: this.issuedNumber,
        limitPerson: this.limitPerson,
        beginTime: this.beginTime,
        endTime: this.endTime
      }
      addBusinessCoupon(data).then(res => {
        this.$toast('创建成功')
        this.$router.replace('/couponList')
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    clickSubmit () {
      if (!this.isPrivacy) {
        this.$toast('点击同意即表示您已阅读并同意《易批花供应商店铺优惠券发放协议》')
        return
      }
      if (!this.title) {
        this.$toast('请输入券的名称')
        return
      }
      if (!this.amount) {
        this.$toast('请输入券的面值')
        return
      }
      if (!this.minAmount) {
        this.$toast('请输入用券最低订单金额')
        return
      }
      if (!this.issuedNumber) {
        this.$toast('请输入发行张数')
        return
      }
      const beginTime = this.beginTime
      if (!beginTime) {
        this.$toast('请选择开始时间')
        return
      }
      const endTime = this.endTime
      if (!endTime) {
        this.$toast('请选择结束时间')
        return
      }
      if (getTimeMaximum(beginTime, endTime)) {
        this.$toast('结束时间不能小于开始时间')
        return
      }
      this.show = true
    },
    limitReduce () {
      const limitPerson = this.limitPerson - 1
      if (limitPerson <= 0) {
        return
      }
      this.limitPerson = limitPerson
    },
    limitAdd () {
      this.limitPerson = this.limitPerson + 1
    },
    choiceDate (type) {
      if (type === 'start') {
        this.$refs.startPopup.showPopup()
      } else {
        this.$refs.endPopup.showPopup()
      }
    },
    confirmStartDate (date) {
      this.beginTime = date
    },
    confirmEndDate (date) {
      this.endTime = date
    }
  },
  mounted () {

  }
}
</script>
<style lang="scss" scoped>
.root {
   width: 100vw;
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.container {
   flex-grow: 1;
   position: relative;
   background-color: #F2F2F2;
}

.content {
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
}

.top {
   width: 100%;
   min-height: 3.125rem;
   background-color: #42D18A;
   display: flex;
   align-items: center;
}

.tip {
   color: white;
   font-size: .875rem;
   margin: 0 .5rem;
}

.bottom {
   width: 100%;
   height: 3.75rem;
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
}

.agree {
   display: flex;
   flex-direction: row;
   align-items: center;
   color: #666666;
   font-size: .875rem;
}

.ck {
   margin: .625rem;
}

.privacy {
   color: #0000ff;
}

.release {
   font-size: 1.125rem;
   text-align: center;
   color: white;
   width: 6.25rem;
   height: 2.875rem;
   line-height: 2.875rem;
   font-weight: 500;
   border-radius: 6%;
   margin: 0 .375rem;
}

.release-true {
   background-color: #42D18A;
}

.release-false {
   background-color: #D3D3D3;
}

.content-box {
   margin: .5rem;
   background-color: white;
   border-radius: 2%;
   padding: .625rem;
   font-size: .875rem;
}

.coupon-group {
   display: flex;
   flex-direction: row;
   margin: .75rem 0;
}

.coupon-checked {
   padding: .375rem .625rem;
   color: white;
   font-size: .875rem;
   margin-right: .375rem;
   border-radius: 8%;
   background-color: #42D18A;
}

.content-tip {
   margin: .5rem 0;
   font-size: .875rem;
}

.detail {
   display: flex;
   flex-direction: row;
   align-items: center;
   margin: .75rem 0;
}

.detail-title {
   width: 20%;
}

.detail-content {
   width: 80%;
}

input {
   background: transparent;
   border: none;
   height: 1.25rem;
}

.detail-content::placeholder {
   color: #9A9A9A;
}

.limit-box {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: flex-end;
}

.img-limit {
   width: 1.1875rem;
   height: auto;
}

.limit-value {
   width: 2.5rem;
   text-align: center;
}

.img-right {
   width: .375rem;
   height: auto;
}

.choice-date {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
}

.sure-popup {
   border-radius: .375rem;
   width: 80%;
}

.sure-popup-title {
   text-align: center;
   font-size: 1.25rem;
   font-weight: 600;
   margin: 1.375rem 0;
}

.sure-popup-content {
   margin: 1rem;
}

.sure-popup-tip {
   color: #D32104;
   margin: 1rem;
   margin-top: -0.25rem;
}

.divider {
   background: #ececec;
   width: 100%;
   height:1.5px;
}

.sure-popup-operation {
   display: flex;
}

.operation-cannel {
   height: 2.875rem;
   flex-grow: 1;
   line-height: 2.875rem;
   font-weight: bold;
   text-align: center;
   font-size: 1.125rem;
}

.operation-ok {
   height: 2.875rem;
   flex-grow: 1;
   line-height: 2.875rem;
   font-weight: bold;
   text-align: center;
   font-size: 1.125rem;
   color: #42D18A;
}

.divider-h{
   background:#ececec;
   height: 2.875rem;
   width: 1.5px;
}
</style>
