/**
 * 时间操作工具类
 */

/**
 * 判断哪个时间大一些，如果左边大于右边则返回true，否则false
 * @param {String} beginTime
 * @param {String} endTime
 */
export const getTimeMaximum = (beginTime, endTime) => {
  const stime = Date.parse(new Date(beginTime))
  const etime = Date.parse(new Date(endTime))
  return stime > etime
}

/**
 * 获取传入时间多少小时后的时间
 *
 * @param {String} timeStr
 * @param {Number} laterHours
 */
export const getAfterHoursTime = (timeStr, laterHours, fmt) => {
  const date = new Date(timeStr)
  date.setHours(date.getHours() + laterHours)
  return date.Format(fmt)
}

Date.Format = function (fmt) {
  const o = {
    'M+': this.getMonth() + 1, // 月份
    'd+': this.getDate(), // 日
    'H+': this.getHours(), // 小时
    'm+': this.getMinutes(), // 分
    's+': this.getSeconds(), // 秒
    'q+': Math.floor((this.getMonth() + 3) / 3), // 季度
    S: this.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
  for (const k in o) { if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length))) }
  return fmt
}
